import React from 'react';
import './LandingPage.scss';
import { Link } from 'react-router-dom';
import { Button, Col, Input, Row } from 'reactstrap';
import useAuth from '../../hooks/useAuth';

const LandingPage = () => {
  const { auth } = useAuth();

  return (
    <div className="lp-container">
      <section className="lp-hero">
        <div className="navbar">
          <div className="logo"></div>
          <div className="menu">
            <Link to="/">Beranda</Link>
            <Link to="/">Kontak</Link>
            <Link to="/">Tentang</Link>
            <Link to="/">Panduan</Link>

            {auth ? (
              <Link to="/dashboard">
                <Button color="success">Dashboard</Button>
              </Link>
            ) : (
              <>
                <Link to="/auth/register">
                  <Button color="primary" outline>
                    Register
                  </Button>
                </Link>
                <Link to="/auth/login">
                  <Button color="success">Masuk</Button>
                </Link>
              </>
            )}
          </div>
        </div>
        <div className="title__search">
          <div className="title">
            <h2>Pelelangan Umum </h2>
            <div className="title-line" />
          </div>
          <div className="search">
            <Input type="text" placeholder="Kata kunci"></Input>
            <Input type="select">
              <option value="">Semua KKS</option>
            </Input>
            <Input type="select">
              <option value="">Bidang Usaha</option>
            </Input>
            <Button type="button" color="success">
              Cari
            </Button>
          </div>
        </div>
      </section>
      {/* <section className="lp-category">
        <div className="content"></div>
      </section> */}
      <section className="items">
        <Row>
          <Col sm="12" lg="6">
            <div className="bg-white rounded-4 p-3">
              <h4>Penyediaan Jasa Tenaga Kerja Kontrak</h4>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus, distinctio a
                tenetur in veritatis deleniti. Mollitia magnam officiis exercitationem, accusantium
                natus dicta assumenda autem minima error. Suscipit et, veritatis debitis laudantium
                esse labore eaque vel, facilis, tempore eum hic tempora!
              </p>
              <Button type="button" color="success" outline>
                Lebih Lanjut
              </Button>
            </div>
          </Col>
          <Col sm="12" lg="6">
            <div className="bg-white rounded-4 p-3">
              <h4>Penyediaan Jasa Tenaga Kerja Kontrak</h4>
              <p>
                Lorem ipsum dolor sit amet consectetur adipisicing elit. Ducimus, distinctio a
                tenetur in veritatis deleniti. Mollitia magnam officiis exercitationem, accusantium
                natus dicta assumenda autem minima error. Suscipit et, veritatis debitis laudantium
                esse labore eaque vel, facilis, tempore eum hic tempora!
              </p>
              <Button type="button" color="success" outline>
                Lebih Lanjut
              </Button>
            </div>
          </Col>
        </Row>
      </section>
    </div>
  );
};

export default LandingPage;
